@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.portfolio__blog {
    display: flex;
    flex-direction: column;    
}

.portfolio__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 2rem;
}

.portfolio__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.portfolio__blog-container {
    display: flex;
    flex-direction: row;
}

.portfolio__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.portfolio__blog-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .portfolio__blog-container {
        flex-direction: column-reverse;
    }


    .portfolio__blog-container_groupA {
        margin: 2rem 0;
    }

    .portfolio__blog-container_groupA .portfolio__blog-container_article  {
       width: 48%;
    }

    .portfolio__blog-container_groupA .portfolio__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .portfolio__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .portfolio__blog-container_groupA .portfolio__blog-container_article  {
       width: 100%;
    }

    .portfolio__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .portfolio__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.portfolio__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.portfolio__features-container__feature-title{
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.portfolio__features-container__feature-title h1{
    font-size: 18px;
    line-height: 24px;
    font-weight: 800;
    letter-spacing:  -0.04rem;
    font-family: var(--font-family);

    color: #fff;
}

.portfolio__features-container__feature-title div{
    width: 38px;
    height: 3px;

    background: var(--gradient-bar);
}

.portfolio__features-container__feature-text {
    flex: 2 1;
    max-width: 500px;
    display: flex;
    flex-direction: column;
}

.portfolio__features-container__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 30px;

    color: var(--color-text);
}


@media screen and (max-width: 990px) {
    .portfolio__features {
        flex-direction: column;
    }

    .portfolio__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .portfolio__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .portfolio__features-container__feature-title p {
        font-size: 12px;
        line-height: 20px;
    }

    .portfolio__features-container__feature {
        margin: 1rem;
        
    }
}
.portfolio__projectCard-container {
    width: 100%;
    height: 100%;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    transition: 0.4s ease-out
}

.portfolio__projectCard-container:hover {
    transform: translateY(20px);
    background-color: #212d45;
}

.portfolio__projectCard-container-projectLink {
    display: flex;
    justify-content: flex-end;
    padding: 1rem 2rem;
}

.portfolio__projectCard-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.portfolio__projectCard-icon{
    display: flex;
    color: white;
    border-radius: 50px;
    justify-content: flex-end;
    padding: 0rem 2rem 1rem 0rem;
}

.portfolio__projectCard-icon:hover {
    color: #AE67FA ;
}

.portfolio__projectCard-image img {
    width: 100%;
    height: 100%;
}

.portfolio__projectCard-content {
    display: flex;
    flex-direction: column;

    padding: 1rem 1.5rem;
    height: 100%;
}

.portfolio__projectCard-content p {
    font-family: var(--font-family);
    font-size: 15px;
    line-height: 22px;

    color: #fff;
}

.portfolio__projectCard-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    
    color: #fff;
    margin-bottom: 1rem;
    cursor: pointer;
}

.portfolio__projectCard-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .portfolio__projectCard-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}
.portfolio__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.portfolio__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.portfolio__navbar-links_logo {
    margin-right: 2rem;
}

.portfolio__navbar-links_logo img {
    width: 70px;
    height: 30px;
}

.portfolio__navbar-menu-link:hover, 
.portfolio__navbar-links_logo:hover {
    transition: 0.5s ease;
    cursor: pointer;
    transform: scale(1.2);
}

.portfolio__navbar-links_container {
    display: flex;
    flex-direction: row;
}


.portfolio__navbar-links_container p,
.portfolio__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.portfolio__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.portfolio__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.portfolio__navbar-menu svg {
    cursor: pointer;
}

.portfolio__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.portfolio__navbar-menu_container p {
    margin: 1rem 0;
}


@media screen and (max-width: 1050px) {
    .portfolio__navbar-links_container {
        display: none;
    }

    .portfolio__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .portfolio__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .portfolio__navbar {
        padding: 2rem;
    }

    .portfolio__navbar-sign {
        display: none;
    }

    .portfolio__navbar-menu_container {
        top: 20px;
    }
}
.portfolio__socialIcons {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.portfolio__socialIcons-icon {
    color: white;
    border-radius: 50px;
    padding: 10px;
}

.portfolio__socialIcons-icon:hover {
    transition: 0.3s ease;
    background-color: #212d45;
    transform: scale(1.2);
    cursor: pointer;
  }
.portfolio__skill-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top:1rem;
}

.portfolio__skill-container p {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    text-transform: capitalize;
    color: #fff;
    font-family: var(--font-family); 
    cursor: pointer;
    padding-right: 1rem;
}

.portfolio__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--gradient-bar);
    padding: 2rem 6rem 1rem 6rem;
}

.portfolio__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
}

.portfolio__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 90px;
    
}

.portfolio__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;
    text-align: left;
}

.portfolio__footer-links {
    display: flex;
}

.portfolio__footer-links img {
    width: 118px;
    height: 30px;

    margin-bottom: 1rem;
}


.portfolio__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.portfolio__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .portfolio__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .portfolio__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .portfolio__footer-links div {
        margin: 1rem 0;
    }

    .portfolio__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .portfolio__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }

    .portfolio__footer-links{
        align-items: center;
    }
    
    .portfolio__footer-links img {
        visibility: hidden;
    }
}
.portfolio__header {
    display: flex;
    flex-direction: row;
    padding: 0rem 6rem 0rem 6rem;
}

.portfolio__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.portfolio__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.portfolio__header-content p{
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.portfolio__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}

.portfolio__header-content__input button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: #ff4820;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-radius: 5px;
}

.portfolio__header-content__input button:hover {
    transition: 0.3s ease;
    background-color: #212d45;
    transform: scale(1.1);
    background-position: left bottom;
    cursor: pointer;
}

.portfolio__header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolio__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .portfolio__header {
        flex-direction: column;
    }
    
    .portfolio__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .portfolio__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    
    .portfolio__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .portfolio__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .portfolio__header h1{
        font-size: 36px;
        line-height: 48px;
    }
    
    .portfolio__header p{
        font-size: 14px;
        line-height: 24px;
    }

    .portfolio__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}


















.portfolio__about {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}


.portfolio__about-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    
    margin: 0rem 1rem;
}

.portfolio__about-container__bar {
    max-width: 40px;
    height: 3px;

    background: var(--gradient-bar);
}

.portfolio__about-container__skills h1 {
    font-size: 18px;
    line-height: 24px;
    font-weight: 800;
    letter-spacing:  -0.04rem;
    font-family: var(--font-family);

    color: #fff;
    margin-bottom: 1rem;
}

.portfolio__about-container__heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.portfolio__about-container__heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-top:1rem;
}

.portfolio__about-container__skills{
    display: flex;
    flex-direction: row;

    margin: 2rem 2rem;
    
}

.portfolio__about-container__skills-language,
.portfolio__about-container__skills-tech,
.portfolio__about-container__skills-cicd {
    margin-left: 3rem;
}

.portfolio__about-features {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

/* Customizing feature component as per needs */
.portfolio__about-features .portfolio__features-container__feature {
    flex: 1 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.portfolio__about-features .portfolio__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 1024px) {
    .portfolio__about-container {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .portfolio__about-container p {
        margin-top: 1rem;
    }
    .portfolio__about-container__skills {
        margin-top: 1rem;
    }

    
}

@media screen and (max-width: 850px) {
    .portfolio__about-container {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .portfolio__about-container p {
        margin-top: 1rem;
    }
    .portfolio__about-container__skills {
        margin: 0rem;
        flex-direction: column;
    }

    
}


@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .portfolio__features-container__feature {
        flex-direction: column;
    }

    .portfolio__about-container__skills ,
    .portfolio__about-container__skills-language,
    .portfolio__about-container__skills-tech,
    .portfolio__about-container__skills-cicd {
        flex-direction: column;
        margin: 0rem;
    }
    
    /* Customizing feature component as per needs */
    .portfolio__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .portfolio__about-container h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .portfolio__about-features .portfolio__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }

    .portfolio__about-container__skills ,
    .portfolio__about-container__skills-language,
    .portfolio__about-container__skills-tech,
    .portfolio__about-container__skills-cicd {
        flex-direction: column;
        margin: 0rem;
    }
}
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
  
    /* safari 5.1+,chrome 10+ */
  
    /* opera 11.10+ */
  
    /* ie 10+ */
  
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding:  4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }  
}

:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
